
.app-view {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--bg-primary);
  position: absolute;
  top: 0;
  left: 0;
}

.app-header {
  background: linear-gradient(135deg, var(--bg-header) 0%, var(--bg-header-light) 100%);
  padding: 48px 20px 16px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--border-accent);
  flex-shrink: 0;
  -webkit-backdrop-filter: blur(15px);
  backdrop-filter: blur(15px);
  position: relative;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(90deg, transparent, var(--border-accent), transparent);
  }

  .title {
    font-size: 1.4rem;
    font-weight: 800;
    display: flex;
    align-items: center;
    gap: 8px;
    color: var(--text-primary);
    letter-spacing: 0.5px;

    i {
      color: var(--accent-primary);
      padding: 8px;
      border-radius: 10px;
      transition: all 0.35s cubic-bezier(0.34, 1.56, 0.64, 1);

      &:hover {
        background: linear-gradient(135deg, rgba(255, 195, 0, 0.15), rgba(255, 215, 64, 0.1));
        transform: scale(1.15) rotate(-8deg);
      }
    }

    span {
      color: var(--text-primary);
    }
  }

  .refresh-btn {
    background: none;
    border: none;
    color: var(--accent-primary);
    font-size: 1.2rem;
    cursor: pointer;
    padding: 8px;
    border-radius: 8px;
    transition: all 0.3s;

    &:hover {
      background: rgba(255, 195, 0, 0.1);
      transform: rotate(180deg);
    }
  }
}

.app-content {
  flex-grow: 1;
  overflow-y: auto;
  padding: 16px;
  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

// 空状态
.empty-state {
  text-align: center;
  padding: 60px 20px;
  color: var(--text-secondary);

  i {
    font-size: 4rem;
    color: var(--text-placeholder);
    margin-bottom: 20px;
  }

  p {
    font-size: 1rem;
    margin-bottom: 24px;
  }

  .retry-btn {
    background: linear-gradient(135deg, var(--accent-primary), var(--accent-light));
    color: var(--text-primary);
    border: none;
    padding: 12px 32px;
    border-radius: 25px;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 6px 20px rgba(255, 195, 0, 0.4);
    }
  }
}

// 加载状态
.loading-state {
  text-align: center;
  padding: 60px 20px;
  color: var(--text-secondary);

  .spinner {
    width: 50px;
    height: 50px;
    border: 4px solid var(--border-color);
    border-top-color: var(--accent-primary);
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin: 0 auto 20px;
  }

  p {
    font-size: 1rem;
  }
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

// 标签页
.tabs-container {
  display: flex;
  gap: 10px;
  margin-bottom: 16px;
  padding: 6px;
  background: var(--bg-card);
  border-radius: 16px;
  box-shadow: 0 2px 8px rgba(255, 195, 0, 0.1);
  border: 1px solid rgba(255, 195, 0, 0.2);

  .tab-item {
    flex: 1;
    padding: 12px 16px;
    border: none;
    background: transparent;
    border-radius: 12px;
    font-weight: 700;
    font-size: 14px;
    cursor: pointer;
    transition: all 0.3s;
    color: var(--text-secondary);
    text-align: center;

    &:hover {
      color: var(--text-primary);
      background: rgba(255, 195, 0, 0.1);
    }

    &.active {
      background: linear-gradient(135deg, var(--accent-primary), var(--accent-light));
      color: var(--text-primary);
      box-shadow: 0 4px 15px rgba(255, 195, 0, 0.3);
    }
  }
}

// 状态卡片
.status-card {
  background: var(--bg-card);
  border-radius: 20px;
  padding: 20px;
  margin-bottom: 16px;
  box-shadow: var(--shadow-md);
  border: 1px solid var(--border-accent);

  .status-header {
    margin-bottom: 20px;

    .basic-info {
      .girl-name {
        font-size: 28px;
        font-weight: 800;
        margin: 0 0 8px 0;
        color: var(--text-primary);
      }

      .sub-info {
        display: flex;
        align-items: center;
        gap: 8px;
        color: var(--text-secondary);
        font-size: 14px;

        .dot {
          color: var(--text-placeholder);
        }
      }
    }

    .badges {
      display: flex;
      gap: 8px;
      margin-top: 12px;

      .badge {
        padding: 4px 12px;
        border-radius: 50px;
        font-size: 11px;
        font-weight: 700;

        &.badge-hot {
          background: var(--badge-danger-gradient);
          color: white;
        }

        &.badge-type {
          background: var(--badge-info-gradient);
          color: white;
        }
      }
    }
  }

  .order-status-row {
    display: flex;
    gap: 16px;
    margin-bottom: 16px;
    padding: 12px;
    background: var(--bg-card);
    border-radius: 12px;

    .status-item {
      display: flex;
      align-items: center;
      gap: 8px;

      .status-icon {
        font-size: 18px;

        &.status-active {
          color: var(--status-success);
        }
        &.status-completed {
          color: var(--status-info);
        }
        &.status-pending {
          color: var(--status-warning);
        }
        &.status-normal {
          color: var(--status-success);
        }
        &.status-warning {
          color: var(--status-warning);
        }
        &.status-danger {
          color: var(--status-danger);
        }
      }

      .status-text {
        font-size: 14px;
        font-weight: 600;
        color: var(--text-primary);
      }
    }
  }

  .rating-section {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 20px;
    padding: 12px;
    background: var(--bg-badge);
    border-radius: 12px;

    .stars {
      display: flex;
      gap: 4px;

      .star {
        font-size: 16px;
        color: var(--border-color);

        &.active {
          color: var(--accent-primary);
        }
      }
    }

    .affection-score {
      font-size: 14px;
      font-weight: 700;
      color: var(--text-secondary);
    }
  }

  .price-section {
    text-align: center;
    padding: 16px;
    background: var(--bg-badge);
    border-radius: 12px;
    margin-bottom: 20px;

    .price-label {
      font-size: 12px;
      color: var(--text-placeholder);
      margin-bottom: 8px;
    }

    .price-value {
      font-size: 32px;
      font-weight: 900;
      background: var(--badge-danger-gradient);
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      line-height: 1;
      margin-bottom: 8px;
    }

    .package-name {
      font-size: 14px;
      color: var(--text-secondary);
      font-weight: 600;
      margin-bottom: 12px;
    }

    .features-tags {
      display: flex;
      flex-wrap: wrap;
      gap: 6px;
      justify-content: center;

      .feature-tag {
        padding: 4px 12px;
        background: var(--accent-primary);
        color: var(--text-primary);
        border-radius: 50px;
        font-size: 11px;
        font-weight: 600;
        box-shadow: 0 2px 4px rgba(255, 195, 0, 0.3);
      }
    }
  }

  .metrics-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;

    /* 平板端：4列 */
    @media (min-width: 481px) {
      grid-template-columns: repeat(4, 1fr);
    }

    .metric-card {
      text-align: center;
      padding: 16px;
      background: var(--bg-card);
      border-radius: 12px;
      transition: all 0.3s;

      &:hover {
        transform: translateY(-4px);
        box-shadow: 0 8px 25px rgba(255, 195, 0, 0.2);
      }

      .metric-label {
        font-size: 13px;
        color: var(--text-secondary);
        margin-bottom: 8px;
        font-weight: 600;
      }

      .metric-value {
        font-size: 28px;
        font-weight: 900;
        color: var(--text-primary);
        margin-bottom: 8px;

        .metric-unit {
          font-size: 14px;
          color: var(--text-secondary);
          font-weight: 700;
        }
      }

      .progress-bar {
        width: 100%;
        height: 6px;
        background: var(--border-color);
        border-radius: 50px;
        overflow: hidden;

        .progress-fill {
          height: 100%;
          border-radius: 50px;
          transition: width 0.8s;
        }
      }
    }
  }
}

// 详情区域
.detail-section {
  .detail-card {
    background: var(--bg-card);
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
    border: 1px solid rgba(255, 195, 0, 0.15);

    .accordion-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 16px 20px;
      cursor: pointer;
      background: var(--bg-card);
      transition: all 0.3s;
      font-weight: 700;
      color: var(--text-primary);

      &:hover {
        background: var(--bg-badge);
      }

      &.active {
        background: var(--bg-badge);

        .accordion-arrow {
          transform: rotate(180deg);
        }
      }

      .accordion-icon {
        color: var(--accent-primary);
        margin-right: 8px;
      }

      .accordion-arrow {
        color: var(--text-placeholder);
        transition: transform 0.3s;
      }
    }

    .accordion-content {
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.3s ease-out;

      &.show {
        max-height: 2000px;
      }
    }

    .accordion-body {
      padding: 20px;
      background: var(--bg-card);
    }
  }

  .detail-group {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }

    .group-title {
      font-size: 16px;
      font-weight: 700;
      color: var(--text-primary);
      margin: 0 0 16px 0;
      display: flex;
      align-items: center;
      gap: 8px;

      i {
        color: var(--accent-primary);
      }
    }

    // 字段名称统一样式（既适合浅色也适合深色模式）
    .field-label,
    .feature-label,
    .desc-label,
    .stat-label,
    .psychology-label,
    .compact-key,
    .info-key,
    .section-item-label {
      color: #3b82f6 !important; // blue-500，既适合浅色也适合深色模式
      font-weight: 600;
    }

    // 可折叠分组样式
    &.collapsible-group {
      margin-bottom: 12px;
      border: 1px solid var(--border-accent);
      border-radius: 16px;
      overflow: hidden;
      background: var(--bg-card);

      .collapsible-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 16px 20px;
        cursor: pointer;
        background: var(--bg-card);
        transition: all 0.3s;

        &:hover {
          background: var(--bg-badge);
        }

        .group-title {
          font-size: 16px;
          font-weight: 700;
          color: var(--text-primary);
          margin: 0;
          display: flex;
          align-items: center;
          gap: 8px;

          i {
            color: var(--accent-primary);
          }
        }

        .accordion-arrow {
          color: var(--text-placeholder);
          transition: transform 0.3s;
          font-size: 14px;

          &.active {
            transform: rotate(180deg);
            color: var(--accent-primary);
          }
        }
      }

      .collapsible-content {
        padding: 0 20px 16px 20px;
        transition: all 0.3s ease;
      }

      // 紧凑信息网格样式
      .compact-info-grid {
        display: flex;
        flex-direction: column;
        gap: 12px;

        .compact-row {
          display: flex;
          align-items: flex-start;
          gap: 12px;
          padding: 12px;
          background: var(--bg-card);
          border-radius: 10px;

          &.full-width {
            flex-direction: column;
            gap: 8px;
          }

          .compact-key {
            font-size: 13px;
            font-weight: 600;
            color: var(--text-primary);
            min-width: 80px;
            flex-shrink: 0;
          }

          .compact-value {
            font-size: 14px;
            font-weight: 500;
            color: var(--text-secondary);
            line-height: 1.5;
            flex: 1;

            &.long-text {
              font-size: 13px;
              line-height: 1.6;
              color: var(--text-primary);
              word-break: break-word;
              white-space: pre-wrap;
            }
          }
        }
      }

      // 心理状态内容样式
      .psychology-content {
        .psychology-item {
          background: linear-gradient(135deg, rgba(255, 195, 0, 0.08), rgba(255, 215, 64, 0.05));
          border: 1px solid rgba(255, 195, 0, 0.2);
          border-radius: 12px;
          padding: 16px;
          position: relative;
          overflow: hidden;

          &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            height: 3px;
            background: linear-gradient(90deg, var(--accent-primary), var(--accent-light));
          }

          .psychology-label {
            display: flex;
            align-items: center;
            gap: 8px;
            font-size: 14px;
            font-weight: 700;
            color: var(--accent-primary);
            margin-bottom: 12px;

            i {
              font-size: 16px;
            }
          }

          .psychology-text {
            font-size: 14px;
            line-height: 1.6;
            color: var(--text-primary);
            font-style: italic;
            padding: 12px;
            background: var(--bg-card);
            border-radius: 8px;
            border-left: 4px solid var(--accent-primary);
            word-break: break-word;
            white-space: pre-wrap;
          }
        }
      }

      // 身体特征网格样式
      .body-feature-grid {
        .feature-row {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          gap: 12px;
          margin-bottom: 16px;

          /* 平板端：4列 */
          @media (min-width: 481px) {
            grid-template-columns: repeat(4, 1fr);
          }

          .feature-item {
            display: flex;
            align-items: center;
            gap: 12px;
            padding: 14px;
            background: var(--bg-card);
            border-radius: 12px;
            border: 1px solid rgba(255, 195, 0, 0.15);
            transition: all 0.3s ease;

            &:hover {
              transform: translateY(-2px);
              box-shadow: 0 6px 20px rgba(255, 195, 0, 0.15);
              border-color: var(--accent-primary);
            }

            .feature-icon {
              width: 28px;
              height: 28px;
              border-radius: 6px;
              background: linear-gradient(135deg, var(--accent-primary), var(--accent-light));
              display: flex;
              justify-content: center;
              align-items: center;
              flex-shrink: 0;

              i {
                font-size: 12px;
                color: white;
              }
            }

            .feature-content {
              flex: 1;
              min-width: 0;

              .feature-label {
                font-size: 11px;
                font-weight: 600;
                color: var(--text-secondary);
                margin-bottom: 2px;
              }

              .feature-value {
                font-size: 14px;
                font-weight: 700;
                color: var(--text-primary);
              }
            }
          }
        }

        .feature-description {
          display: flex;
          flex-direction: column;
          gap: 12px;

          .desc-item {
            background: var(--bg-card);
            border-radius: 10px;
            padding: 12px;
            border-left: 4px solid var(--accent-primary);

            .desc-label {
              display: flex;
              align-items: center;
              gap: 8px;
              font-size: 13px;
              font-weight: 600;
              color: var(--accent-primary);
              margin-bottom: 8px;

              i {
                font-size: 14px;
              }
            }

            .desc-text {
              font-size: 13px;
              line-height: 1.6;
              color: var(--text-primary);
              padding-left: 22px;
              word-break: break-word;
              white-space: pre-wrap;
            }
          }
        }
      }

      // 性经验与服务统计样式
      .experience-stats {
        .stats-section {
          margin-bottom: 20px;

          &:last-child {
            margin-bottom: 0;
          }

          .section-title {
            display: flex;
            align-items: center;
            gap: 8px;
            font-size: 14px;
            font-weight: 700;
            color: var(--accent-primary);
            margin-bottom: 12px;
            padding: 8px 12px;
            background: linear-gradient(135deg, rgba(255, 195, 0, 0.1), rgba(255, 215, 64, 0.05));
            border-radius: 8px;
            border: 1px solid rgba(255, 195, 0, 0.2);

            i {
              font-size: 16px;
            }
          }

          .stats-grid {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 10px;

            /* 平板端：3列 */
            @media (min-width: 481px) {
              grid-template-columns: repeat(3, 1fr);
            }

            /* 大屏端：5列 */
            @media (min-width: 1024px) {
              grid-template-columns: repeat(5, 1fr);
            }

            .stat-item {
              display: flex;
              align-items: center;
              gap: 10px;
              padding: 12px;
              background: var(--bg-card);
              border-radius: 10px;
              border: 1px solid rgba(255, 195, 0, 0.12);
              transition: all 0.3s ease;

              &:hover {
                transform: translateY(-1px);
                box-shadow: 0 4px 12px rgba(255, 195, 0, 0.1);
                border-color: var(--accent-primary);
              }

              .stat-icon {
                width: 32px;
                height: 32px;
                border-radius: 8px;
                background: linear-gradient(135deg, var(--accent-primary), var(--accent-light));
                display: flex;
                justify-content: center;
                align-items: center;
                flex-shrink: 0;

                i {
                  font-size: 14px;
                  color: white;
                }
              }

              .stat-content {
                flex: 1;
                min-width: 0;

                .stat-label {
                  font-size: 11px;
                  font-weight: 600;
                  color: var(--text-secondary);
                  margin-bottom: 2px;
                  line-height: 1.2;
                }

                .stat-value {
                  font-size: 14px;
                  font-weight: 700;
                  color: var(--text-primary);
                  line-height: 1.2;
                }
              }
            }
          }
        }
      }
    }

    .clothing-grid {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 12px;

      /* 平板端：3列 */
      @media (min-width: 481px) {
        grid-template-columns: repeat(3, 1fr);
      }

      /* 大屏端：4列 */
      @media (min-width: 1024px) {
        grid-template-columns: repeat(4, 1fr);
      }

      .clothing-item {
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 12px;
        background: var(--bg-card);
        border-radius: 10px;

        .clothing-icon {
          font-size: 18px;
          color: var(--accent-primary);
          flex-shrink: 0;
        }

        .clothing-key {
          font-size: 12px;
          color: var(--text-primary);
          flex: 1;
        }

        .clothing-value {
          font-size: 13px;
          font-weight: 600;
          color: var(--text-primary);
        }
      }
    }

    .info-rows {
      display: grid;
      gap: 10px;

      .info-row {
        display: flex;
        flex-direction: column;
        gap: 8px;
        padding: 12px 16px;
        background: var(--bg-card);
        border-radius: 10px;

        .info-key {
          font-size: 13px;
          color: var(--text-primary);
          font-weight: 600;
        }

        .info-val {
          font-size: 14px;
          font-weight: 700;
          color: var(--text-primary);
          line-height: 1.5;
          word-break: break-word;
          white-space: pre-wrap;
        }
      }
    }

    .empty-text {
      text-align: center;
      color: var(--text-placeholder);
      padding: 20px;
    }
  }
}

// 底部导航
.nav-bar {
  display: flex;
  border-top: 1px solid var(--border-color);
  background: linear-gradient(135deg, var(--bg-header) 0%, var(--bg-header-light) 100%);
  padding: 8px 0;
  flex-shrink: 0;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(90deg, transparent, var(--border-color), transparent);
  }

  .nav-item {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--text-secondary);
    font-size: 0.8rem;
    padding: 4px 0;
    cursor: pointer;
    transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
    border-radius: 8px;
    margin: 0 4px;

    &.active {
      color: var(--text-primary);

      i {
        color: var(--accent-primary);
        transform: scale(1.15) translateY(-2px);
      }
    }

    &:hover:not(.active) {
      color: var(--text-primary);
      transform: translateY(-1px);
    }

    i {
      font-size: 1.25rem;
      margin-bottom: 4px;
      transition: all 0.25s cubic-bezier(0.34, 1.56, 0.64, 1);
    }
  }
}
