
.app-view {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: var(--bg-primary);
  position: absolute;
  top: 0;
  left: 0;
  transition: background-color 0.3s ease;
}

.app-header {
  background: linear-gradient(135deg, var(--bg-header) 0, var(--bg-header-light) 100%);
  padding: 35px 16px 12px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--border-color);
  flex-shrink: 0;
  transition: all 0.3s ease;

  .title {
    font-size: 1.3rem;
    font-weight: 700;
    display: flex;
    align-items: center;
    gap: 8px;
    color: var(--text-primary);

    i {
      color: var(--accent-primary);
    }
  }
}

.app-content {
  flex-grow: 1;
  overflow-y: auto;
  padding: 16px;
  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.user-profile-card {
  background: var(--bg-card);
  border-radius: 16px;
  padding: 20px;
  margin-bottom: 16px;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-accent);
  display: flex;
  align-items: center;
  gap: 16px;
  transition: all 0.3s ease;

  &:hover {
    box-shadow: var(--shadow-md);
    transform: translateY(-2px);
  }

  .avatar-wrapper {
    position: relative;

    .avatar {
      width: 80px;
      height: 80px;
      border-radius: 50%;
      background: linear-gradient(135deg, var(--accent-primary) 0, var(--accent-dark) 100%);
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 32px;
      color: white;
      box-shadow: var(--shadow-md);
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
      }

      i {
        text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
      }
    }

    .avatar-badge {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 28px;
      height: 28px;
      border-radius: 50%;
      background: linear-gradient(135deg, var(--accent-light) 0%, var(--accent-primary) 100%);
      display: flex;
      justify-content: center;
      align-items: center;
      border: 2px solid var(--bg-card);
      box-shadow: var(--shadow-sm);

      i {
        font-size: 14px;
        color: white;
      }
    }
  }

  .user-info {
    flex-grow: 1;

    .user-name {
      font-size: 24px;
      font-weight: 700;
      margin-bottom: 6px;
      color: var(--text-primary);
    }

    .user-level {
      display: inline-flex;
      align-items: center;
      gap: 6px;
      padding: 4px 12px;
      background: var(--bg-badge);
      border: 1px solid var(--accent-primary);
      border-radius: 50px;
      font-size: 13px;
      font-weight: 600;
      color: var(--accent-dark);
      margin-bottom: 12px;
      transition: all 0.3s ease;

      i {
        color: var(--accent-primary);
      }
    }

    .user-stats {
      display: flex;
      align-items: center;
      gap: 16px;

      .stat-item {
        text-align: center;

        .stat-value {
          font-size: 20px;
          font-weight: 700;
          color: var(--text-primary);
          margin-bottom: 4px;
        }

        .stat-label {
          font-size: 11px;
          color: var(--text-secondary);
        }
      }

      .stat-divider {
        width: 1px;
        height: 30px;
        background: var(--border-color);
      }
    }
  }
}

.card {
  background: var(--bg-card);
  border-radius: 16px;
  padding: 20px;
  margin-bottom: 16px;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-accent);
  transition: all 0.3s ease;

  &:hover {
    box-shadow: var(--shadow-md);
  }

  .card-title {
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    gap: 8px;
    color: var(--text-primary);

    i {
      color: var(--accent-primary);
    }
  }
}

.empty-state {
  text-align: center;
  padding: 40px 20px;
  color: var(--text-secondary);
  line-height: 1.6;

  i {
    font-size: 3rem;
    color: var(--text-placeholder);
    margin-bottom: 16px;
    display: block;
  }
}

.settings-list {
  display: flex;
  flex-direction: column;
  gap: 8px;

  /* 平板端：网格布局 */
  @media (min-width: 481px) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
  }
}

.settings-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  background: var(--bg-item);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.25s ease;

  &:hover {
    background: var(--bg-item-hover);
    transform: translateX(4px);
  }

  .settings-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: var(--bg-badge);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    color: var(--accent-primary);
    border: 1px solid var(--accent-primary);
  }

  .settings-info {
    flex-grow: 1;

    .settings-title {
      font-size: 14px;
      font-weight: 600;
      color: var(--text-primary);
      margin-bottom: 4px;
    }

    .settings-desc {
      font-size: 12px;
      color: var(--text-secondary);
    }
  }

  .settings-toggle,
  .settings-arrow {
    font-size: 18px;
    color: var(--text-secondary);
    transition: all 0.25s ease;

    &:hover {
      color: var(--accent-primary);
    }
  }
}

.nav-bar {
  display: flex;
  border-top: 1px solid var(--border-color);
  background: linear-gradient(135deg, var(--bg-header) 0, var(--bg-header-light) 100%);
  padding: 8px 0;
  flex-shrink: 0;

  .nav-item {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--text-secondary);
    font-size: 0.8rem;
    padding: 4px 0;
    cursor: pointer;
    transition: all 0.25s;
    border-radius: 8px;
    margin: 0 4px;

    &.active {
      color: var(--text-primary);

      i {
        color: var(--accent-primary);
        transform: scale(1.15) translateY(-2px);
      }
    }

    &:hover:not(.active) {
      color: var(--text-primary);
      transform: translateY(-1px);
    }

    i {
      font-size: 1.25rem;
      margin-bottom: 4px;
      transition: all 0.25s;
    }
  }
}

@media (max-width: 480px) {
  .user-profile-card {
    flex-direction: column;
    text-align: center;

    .user-stats {
      justify-content: space-around;
    }
  }
}
