
.splash-screen {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #ffc300 0%, #ff9500 100%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
  overflow: hidden;
  animation: fadeIn 0.8s ease-out;
  margin: 0 auto;

  .bg-decoration {
    position: absolute;
    inset: 0;
    pointer-events: none;

    .circle-bg {
      position: absolute;
      border-radius: 50%;
      background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
      animation: float 6s ease-in-out infinite;

      &.circle-1 {
        width: 300px;
        height: 300px;
        top: -100px;
        right: -100px;
        animation-delay: 0s;
      }

      &.circle-2 {
        width: 200px;
        height: 200px;
        bottom: -50px;
        left: -50px;
        animation-delay: 2s;
      }

      &.circle-3 {
        width: 150px;
        height: 150px;
        top: 20%;
        left: 10%;
        animation-delay: 4s;
      }
    }
  }

  .splash-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0 40px;
    z-index: 1;

    .logo-section {
      margin-bottom: 60px;
      animation: slideInDown 1s ease-out 0.2s both;

      .logo-container {
        text-align: center;

        .logo-icon {
          width: 100px;
          height: 100px;
          background: rgba(255, 255, 255, 0.95);
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: 0 auto 20px;
          box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
          animation: logoBounce 2s ease-in-out infinite;

          i {
            font-size: 2.5rem;
            background: linear-gradient(135deg, #ff6b35, #ff9500);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
          }
        }

        .logo-text {
          .brand-name {
            font-size: 2.5rem;
            font-weight: 900;
            color: white;
            margin: 0 0 8px 0;
            text-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
            letter-spacing: 2px;
          }

          .brand-subtitle {
            font-size: 0.9rem;
            color: rgba(255, 255, 255, 0.9);
            margin: 0;
            font-weight: 500;
            letter-spacing: 1px;
          }
        }
      }
    }

    .slogan-section {
      text-align: center;
      animation: slideInUp 1s ease-out 0.6s both;

      .main-slogan {
        font-size: 1.3rem;
        font-weight: 500;
        color: white;
        margin: 0 0 30px 0;
        line-height: 1.6;
        text-shadow: 0 2px 4px rgba(0, 0, 0, 0.15);
        letter-spacing: 0.5px;
        max-width: 280px;
      }

      .slogan-decoration {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 16px;

        .decoration-line {
          width: 40px;
          height: 2px;
          background: rgba(255, 255, 255, 0.6);
          border-radius: 1px;
        }

        i {
          color: rgba(255, 255, 255, 0.8);
          font-size: 1rem;
          animation: sparkle 2s ease-in-out infinite;
        }
      }
    }
  }

  .footer-section {
    width: 100%;
    padding: 0 40px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    animation: fadeIn 1s ease-out 1s both;

    .version-info {
      span {
        color: rgba(255, 255, 255, 0.7);
        font-size: 0.8rem;
        font-weight: 500;
      }
    }

    .skip-button {
      display: flex;
      align-items: center;
      gap: 8px;
      color: rgba(255, 255, 255, 0.9);
      font-size: 0.9rem;
      font-weight: 500;
      cursor: pointer;
      transition: all 0.3s ease;
      padding: 8px 12px;
      border-radius: 20px;
      background: rgba(255, 255, 255, 0.1);
      -webkit-backdrop-filter: blur(10px);
      backdrop-filter: blur(10px);

      &:hover {
        background: rgba(255, 255, 255, 0.2);
        transform: translateX(4px);
      }

      &.fade-out {
        opacity: 0;
        transform: translateX(10px);
      }

      i {
        font-size: 0.8rem;
      }
    }
  }

  .loading-indicator {
    position: absolute;
    bottom: 80px;
    left: 50%;
    transform: translateX(-50%);
    animation: fadeIn 1s ease-out 1.2s both;

    .loading-dots {
      display: flex;
      gap: 8px;

      .dot {
        width: 8px;
        height: 8px;
        background: rgba(255, 255, 255, 0.7);
        border-radius: 50%;
        animation: loadingDot 1.4s ease-in-out infinite;

        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: 0.2s;
        }
        &:nth-child(3) {
          animation-delay: 0.4s;
        }
      }
    }
  }
}

// 动画定义
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideInDown {
  from {
    opacity: 0;
    transform: translateY(-50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes logoBounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
}

@keyframes sparkle {
  0%,
  100% {
    opacity: 0.7;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.2);
  }
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-15px) rotate(5deg);
  }
}

@keyframes loadingDot {
  0%,
  80%,
  100% {
    opacity: 0.3;
    transform: scale(1);
  }
  40% {
    opacity: 1;
    transform: scale(1.3);
  }
}
