
.error-boundary {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background: var(--bg-primary);
  padding: 40px 20px;

  .error-content {
    text-align: center;
    color: var(--text-secondary);

    i {
      font-size: 4rem;
      color: var(--accent-primary);
      margin-bottom: 20px;
      display: block;
    }

    h3 {
      font-size: 1.5rem;
      font-weight: 700;
      margin: 0 0 12px 0;
      color: var(--text-primary);
    }

    p {
      font-size: 0.9rem;
      color: var(--text-secondary);
      margin: 0 0 24px 0;
    }

    .retry-btn {
      background: linear-gradient(135deg, var(--accent-primary), var(--accent-dark));
      color: white;
      border: none;
      padding: 12px 32px;
      border-radius: 25px;
      font-weight: 700;
      cursor: pointer;
      transition: all 0.3s;
      display: inline-flex;
      align-items: center;
      gap: 8px;

      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 6px 20px rgba(255, 195, 0, 0.4);
      }

      i {
        font-size: 1rem;
        margin: 0;
      }
    }
  }
}
